.about-container {
  padding: 100px 20px;
  max-width: 800px;
  margin: 0 auto;
  color: white;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  width: 100vw;
}

.about-container h1 {
  font-size: 3rem;
  margin-bottom: 30px;
}

.about-container p {
  font-size: 1.2rem;
  line-height: 1.8;
  margin-bottom: 20px;
}

